import React from "react";
import ShimmerProduct from "./shimmer-product";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { collectionFour } from "../../options";

function ShimmerProducts({ columns, option = collectionFour }) {
	const mockItems = Array(columns).fill(1);

	return (
		<Splide options={option}>
			{mockItems.map((_, index) => {
				return (
					<SplideSlide key={index} className="shimmer-products">
						<ShimmerProduct />
					</SplideSlide>
				);
			})}
		</Splide>
	);
}

export default ShimmerProducts;
