import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { getSuppliersList } from "../../api/cache/main";
import ShimmerProducts from "../../components/features/loaders/ShimmerProducts";
import Pagination from "../../components/features/product/details/Pagination";
import SupplierItemOne from "../home/components/suppliers/SupplierItemOne";

function Suppliers() {
	const { t } = useTranslation();
	const [suppliers, setSuppliers] = useState();
	const [loader, setLoader] = useState(true);
	const [totalCount, setTotalCount] = useState();
	const queryString = require("qs");
	const [initialPages, setInitialPages] = useState({
		page: 1,
		size: 20
	});
	const strQuery = queryString.stringify(initialPages);

	useEffect(() => {
		getSuppliersList(strQuery).then(res => {
			setSuppliers(res);
			setTotalCount(res.totalCount);
			setLoader(false);
		});
		window.history.replaceState(null, "", `/suppliers/?` + strQuery);
	}, [strQuery]);

	function toTop() {
		window.scroll({
			top: 0
		});
	}

	return (
		<>
			<div className="suppliers-page">
				<div className="container">
					<div className="suppliers_header">
						<h3>{t("all-suppliers")}</h3>
					</div>
					<div className="row w-100 m-0">
						{!loader ? (
							suppliers.items.map((supplier, i) =>
								supplier.products?.length > 0 ? (
									<div
										key={i}
										className="col-lg-3 col-md-4 col-12 mt-1"
									>
										<SupplierItemOne
											supplier={supplier}
											key={supplier.id}
										/>
									</div>
								) : null
							)
						) : (
							<ShimmerProducts columns={4} className="col-3" />
						)}
					</div>

					{totalCount > 20 ? (
						<div className="page-pagination">
							<Pagination
								pageCount={Math.ceil(
									suppliers.totalCount / initialPages.size
								)}
								onChange={({ selected }) => {
									setLoader(true);
									setInitialPages(prev => ({
										...prev,
										page: 1 + selected
									}));
									toTop();
								}}
							/>
						</div>
					) : null}
				</div>
			</div>
		</>
	);
}

export default Suppliers;
